___CSS_LOADER_EXPORT___.push([module.id, `.container {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    background-color: var(--surface-primary-background);
    height: 100vh;
}

.header-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 20%;
    overflow-y: scroll;
    box-shadow: var(--container-shadow);
}

.header-mobile-container {
    display: none;
}

.header-links-container {
    display: flex;
    flex-direction: column;
}

.body-container {
    width:80%;
    overflow-y: scroll;
}

.hero-container {
    display: flex;
    flex-direction: column;
    box-shadow: var(--container-shadow);
    padding: var(--container-padding);
    gap: var(--spacing-40);
}

.content-container {
    display: flex;
    flex-direction: column;
    gap: var(--gutter);
    padding: var(--container-padding);
}

.card-container {
    display: flex;
    flex-wrap: wrap;
    gap: var(--gutter);
}

@media screen and (max-width: 1200px) {
    .container {
        width: 100%;
        flex-direction: column;
    }
    
    .header-container {
        display: none;
    }

    .header-mobile-container {
        display: flex;
        flex-direction: row;
        box-shadow: 5px 0px 5px 0px rgba(221, 221, 221, 0.60);
        overflow-y: visible;
        z-index: 10000;
        align-items: center;
        justify-content: space-between;
    }

    .body-container{
        width:100%;
    }

