___CSS_LOADER_EXPORT___.push([module.id, `.project-card {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    top: 0px;
    position: relative;
    width: calc((100% - var(--gutter))/2);
    background-color: var(--surface-primary-background);
    border-radius: var(--card-border-radius);
    gap: var(--spacing-20);
    overflow: hidden;
    -webkit-transition: all .2s ease-out;
    -moz-transition: all .2s ease-out;
    -o-transition: all .2s ease-out;
    transition: all .2s ease-out;
}

.project-card:hover {
    top: -2px;
    position: relative;
    -webkit-transition: all .2s ease-out;
    -moz-transition: all .2s ease-out;
    -o-transition: all .2s ease-out;
    transition: all .2s ease-out;
    cursor: pointer;
}

.project-card > .image-wrapper{
    /*delete later*/
    background-color: #ddd;
    height: 300px;
}

.project-card-body {
    display: flex;
    flex-direction: column;
    border-radius: 0 0 var(--card-border-radius) var(--card-border-radius);
}

.project-card-body > .heading-sub {
    color: var(--surface-primary-link);
}
@media screen and (max-width: 768px){
    .project-card {
        width: 100%;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/projectCard.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,eAAe;IACf,sBAAsB;IACtB,QAAQ;IACR,kBAAkB;IAClB,qCAAqC;IACrC,mDAAmD;IACnD,wCAAwC;IACxC,sBAAsB;IACtB,gBAAgB;IAChB,oCAAoC;IACpC,iCAAiC;IACjC,+BAA+B;IAC/B,4BAA4B;AAChC;;AAEA;IACI,SAAS;IACT,kBAAkB;IAClB,oCAAoC;IACpC,iCAAiC;IACjC,+BAA+B;IAC/B,4BAA4B;IAC5B,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,sBAAsB;IACtB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,sEAAsE;AAC1E;;AAEA;IACI,kCAAkC;AACtC;AACA;IACI;QACI,WAAW;IACf;AACJ","sourcesContent":[".project-card {\n    display: flex;\n    flex-wrap: wrap;\n    flex-direction: column;\n    top: 0px;\n    position: relative;\n    width: calc((100% - var(--gutter))/2);\n    background-color: var(--surface-primary-background);\n    border-radius: var(--card-border-radius);\n    gap: var(--spacing-20);\n    overflow: hidden;\n    -webkit-transition: all .2s ease-out;\n    -moz-transition: all .2s ease-out;\n    -o-transition: all .2s ease-out;\n    transition: all .2s ease-out;\n}\n\n.project-card:hover {\n    top: -2px;\n    position: relative;\n    -webkit-transition: all .2s ease-out;\n    -moz-transition: all .2s ease-out;\n    -o-transition: all .2s ease-out;\n    transition: all .2s ease-out;\n    cursor: pointer;\n}\n\n.project-card > .image-wrapper{\n    /*delete later*/\n    background-color: #ddd;\n    height: 300px;\n}\n\n.project-card-body {\n    display: flex;\n    flex-direction: column;\n    border-radius: 0 0 var(--card-border-radius) var(--card-border-radius);\n}\n\n.project-card-body > .heading-sub {\n    color: var(--surface-primary-link);\n}\n@media screen and (max-width: 768px){\n    .project-card {\n        width: 100%;\n    }\n}"],"sourceRoot":""}]);
