    height: 400px;
    position: relative;
    background-color: #666;
}

.hero-overlay {
    background-color: var(--overlay);
    position: absolute;
    width: 100%;
    height: 400px;
}

.hero-image > .hero-link{
    position: absolute;
    z-index: 1000;
}

.hero-link {
    padding: var(--container-padding);
}

.project-summary {
    display: flex;
    flex-direction: row;
    gap: var(--gutter);
}

.project-objective {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-10);
    width: 50%;
}

.project-requirement {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-10);
    width: 50%;
}

.info-block{
    display: flex;
    flex-direction: column;
    gap: var(--spacing-20);
}

/* Mobile, Tablet, Small Laptop*/
@media screen and (max-width: 1200px) {
    .project-summary{
        flex-direction: column;
    }
    
    .project-objective, .project-requirement {
        width: 100%;
    }
