import React from 'react';
import HeroImage from '../../general_sections/heroImage';
import PortfolioProjectHero from './portfolioProjectHero';
import Link from '../../style_guide/link';

const InfoBlock = ({heading, subheading, body}) => {
    return (
        <div className="info-block">
            {heading && <div className="heading-sub">{heading}</div>}
            {subheading && <div className="heading-flag">{subheading}</div>}
            {body && <p className="body-base">{body}</p>}
        </div>
    )
}

export default function PortfolioProject({projectData}) {
    return (
        <div className="body-container">
            <HeroImage/>
            <PortfolioProjectHero 
                pageTitle={projectData.projectName}
                pageRequirement={projectData.projectRequirement}
                pageObjective={projectData.projectObjective}
                isHome={false}
            ></PortfolioProjectHero>
            <div className="content-container">
                {(projectData.projectWriteup).map((item, i)=>{
                    return (
                        <InfoBlock key={i} heading={item.heading} subheading={item.subheading} body={item.body}></InfoBlock>
                    )
                })}
            </div>
            <div className="content-container">
                <p>next project</p>
                <Link linkText={projectData.projectName} style="link-primary" url=".." hasRightIcon={true}/>
            </div>
        </div>
    )
} 