    --spacing-80: 80px;
    --spacing-60: 60px;
    --spacing-40: 40px;
    --spacing-30: 30px;
    --spacing-25: 25px;
    --spacing-20: 20px;
    --spacing-15: 15px;
    --spacing-10: 10px;
    --spacing-5: 5px;

    /* fonts */
    --base-font-size: 15px;
    --base-font-height: 20px;
    --base-font-Family: "Jost";
    --heading-font-Family: "Josefin Sans";
    --headline-font-Family: "Italiana";

    /* base font*/
    font-size: var(--base-font-size);
    line-height: var(--base-font-height);
