    --button-primary-text: var(--brand-purple-300);

    --button-secondary-background: var(--brand-white);
    --button-secondary-text: var(--brand-green-300);

    --button-primary-shadow: var(--brand-purple-100);
    --button-secondary-shadow: var(--brand-green-100);

    --link-primary-text: var(--brand-purple-300);
    --link-secondary-text: var(--brand-green-300);

    /* motif */
    --motif-primary: var(--brand-purple-300);
    --motif-secondary: var(--brand-green-300);
    --motif-tertiary: var(--brand-purple-200);

