    --card-shadow: 0px 0px 2.5px 0px var(--brand-grayscale-200);
    --card-shadow-hover: 0px 5px 10px -2.5px var(--brand-grayscale-200);

    /* base spacing */
    --gutter: var(--spacing-60);
    --container-padding: var(--spacing-80);
    --button-spacing: var(--spacing-20) var(--spacing-30) var(--spacing-15) var(--spacing-30);
    --card-padding: var(--spacing-40);

    /* resume */
    --resume-font-Family: "Arial";
    --resume-gutter: var(--spacing-20);

    /* border radius */
    --button-border-radius: 0px;
    --card-border-radius: 0px;
    --icon-border-radius: 50%;

}

@media screen and (max-width: 768px) {
